html {
  text-align: center;
  background-color: #ed1c24;
  min-height: 100%;
  overflow-y: scroll;
}
* {
  font-family: 'Work Sans', sans-serif;
}
a {
  color: black;
  text-decoration: none;
}
input {
  background: transparent;
  border: none;
  border-bottom: 1px dashed #ed1c24;
  padding: 8px 10px;
}
button {
  background: #ed1c24;
  border: 1px solid red;
  padding: 8px 10px 8px 10px;
  border-radius: 0 5px 5px 0;
  color: white;
}
#mailing-list {
  display: -ms-flexbox;
  display: flex;
}
#mailing-list input {
  margin-left: auto;
}
#mailing-list button {
  margin-right: auto;
}
#parliament {
  max-width: 80%;
  max-height: 80%;
  bottom: -15px;
  left: 0;
  position: absolute;
  -webkit-animation: show-up 0.6s ease-out;
          animation: show-up 0.6s ease-out;
}
#title {
  font-family: 'Work Sans', sans-serif;
  color: white; 
  letter-spacing: 5px;
  font-size: 2em;
  /* animation: fade-in 1.6s;   */
  position: fixed;
  top: 2em;
  left: 0;
  width: 100%;
}
#title h1, #title h5 {
  margin: 0;
  font-weight: 300;
}
h6 {
  font-weight: lighter;
  text-align: right;
  margin: 4em 10%;
  font-size: 0.5em;
}

#description {
  position: absolute;
  width: 80%;
  top: 100%;
  padding: 20px 10%;
  background-color: white;
}

.faq {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.qa {
  min-width: 10%;
  padding: 0em 2em;
  margin: auto;
  max-width: 15em;
}
#bottom-links {
  margin: auto;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-weight: bold;
}

hr {
  background-color: #ed1c24;
  max-width: 30%;
  height: 5px;
  border: none;
  margin: 50px auto;
}


@-webkit-keyframes show-up {
  from {
    bottom: -100%;
  }
  80% {
    bottom: 0px;
  }
  to {
    bottom: -15px;
  }
}


@keyframes show-up {
  from {
    bottom: -100%;
  }
  80% {
    bottom: 0px;
  }
  to {
    bottom: -15px;
  }
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  35% {
    opacity: 0;
    font-size: 0.1em;
  }
  to {
    opacity: 1;
    font-size: 2em;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  35% {
    opacity: 0;
    font-size: 0.1em;
  }
  to {
    opacity: 1;
    font-size: 2em;
  }
}

@media only screen and (max-width: 600px) {
  #title h1 {
      font-size: 1.5em;
      font-weight: 400;
  }
}


